:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
}
.nav-link-icon {
  display: flex;
  align-items: center;
  gap: 6px;
  overflow: hidden;
  position: relative;
}
.nav-link-icon,
#login-dropdown {
  color: rgba(255,255,255,0.7);
  transition: color 0.5s ease, opacity 0.5s ease;
}
.nav-link-icon:hover,
.nav-link-icon.active,
#login-dropdown:hover,
#login-dropdown.show {
  color: rgba(255, 255, 255, 1);
  opacity: 1;
}
.nav-link-icon .nav-link-text {
  max-width: 0;
  opacity: 0;
  white-space: nowrap;
  transform: translateX(-10px);
  transition: all 0.5s ease;
}
.nav-link-icon:hover .nav-link-text {
  max-width: 200px;
  opacity: 1;
  transform: translateX(0);
}
#login-dropdown::after {
  display: none !important;
}
.drop-down::after {
  display: none !important;
}
.nav-dropdown {
  position: relative;
}
.navbar .dropdown-menu {
  display: block !important;
  position: absolute;
  transform-origin: top;
  transform: scaleY(0);
  opacity: 0;
  transition: transform 0.25s ease, opacity 0.25s ease;
  pointer-events: none;
  overflow: hidden;
}
.navbar .dropdown.show .dropdown-menu {
  transform: scaleY(1);
  opacity: 1;
  pointer-events: auto;
}
.custom-modal {
  height: 50vh;
}
.custom-modal .modal-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  gap: 1rem;
}
.sign-out-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
}
.custom-footer {
  color: white;
  text-align: center;
}
.custom-card-border {
  border-width: 3px;
}
